<template>
	<div class="container admin-container">
		<section class="section__list">
			<h4 class="section__title">
				<span>고객사 현황</span>
			</h4>
			<div class="lst-wrap">
				<div class="input-box">
					<select v-model="pagingVO.searchKind">
						<option value="1">고객사명</option>
						<option value="2">대표명</option>
						<option value="3">사업자번호</option>
					</select>
					<input class="input " type="text" placeholder="입력하세요" v-model="pagingVO.searchKeyword" />
					<button type="button" class="button">
						<span class="icon icon-search"></span>
						조회
					</button>
				</div>
				<div class="table-wrap">
					<table class="table table-hover">
						<thead>
							<tr>
								<th>No.</th>
								<th>고객사명</th>
								<th>사업자번호</th>
								<th>계약기간</th>
								<th>계약해지</th>
							</tr>
						</thead>
						<tbody>
							<tr class="row" v-for="(item, idx) in clientList" :key="item.clientId">
								<td>{{ idx + 1 }}</td>
								<td>{{ item.clientNm }}</td>
								<td>{{ item.bizNo | bizNoFilter }}</td>
								<td>
									{{ item.contractDateFrom | dateFilter }} ~ {{ item.contractDateTo | dateFilter }}
								</td>
								<td>
									<button type="button" class="button-state">
										<span
											class="button-txt button-txt__true on"
											:class="{
												'button-txt__true on': item.clientStatus == '2',
												'button-txt__false on': item.clientStatus == '3',
											}"
											>계약중</span
										>
									</button>
								</td>
							</tr>
						</tbody>
					</table>
				</div>
			</div>
		</section>
		<section class="section__contents">
			<div class="inner-wrap">
				<div class="section-top">
					<h3 class="section__title">고객사 모듈 등록</h3>
				</div>
				<article class="article">
					<div class="article-top">
						<strong class="content__title">
							고객사명
						</strong>
					</div>
					<div class="article-content">
						<div class="table-wrap">
							<table class="table table-hover">
								<thead>
									<tr>
										<th>No.</th>
										<th>모듈명</th>
										<th>사용여부</th>
									</tr>
								</thead>
								<tbody>
									<tr class="row">
										<td>1</td>
										<td>유연근무</td>
										<td>
											<button type="button" class="button-state">
												<span class="button-txt button-txt__true on">사용</span>
												<span class="button-txt button-txt__false">미사용</span>
											</button>
										</td>
									</tr>
									<tr class="row">
										<td>2</td>
										<td>영업CRM</td>
										<td>
											<button type="button" class="button-state">
												<span class="button-txt button-txt__true on">사용</span>
												<span class="button-txt button-txt__false">미사용</span>
											</button>
										</td>
									</tr>
									<tr class="row">
										<td>3</td>
										<td>비용정산</td>
										<td>
											<button type="button" class="button-state">
												<span class="button-txt button-txt__true">사용</span>
												<span class="button-txt button-txt__false on">미사용</span>
											</button>
										</td>
									</tr>
									<tr class="row">
										<td>4</td>
										<td>WEB 주문</td>
										<td>
											<button type="button" class="button-state">
												<span class="button-txt button-txt__true">사용</span>
												<span class="button-txt button-txt__false on">미사용</span>
											</button>
										</td>
									</tr>
									<tr class="row">
										<td>5</td>
										<td>구매 SCM</td>
										<td>
											<button type="button" class="button-state">
												<span class="button-txt button-txt__true">사용</span>
												<span class="button-txt button-txt__false on">미사용</span>
											</button>
										</td>
									</tr>
									<tr class="row">
										<td>6</td>
										<td>EIS</td>
										<td>
											<button type="button" class="button-state">
												<span class="button-txt button-txt__true on">사용</span>
												<span class="button-txt button-txt__false">미사용</span>
											</button>
										</td>
									</tr>
								</tbody>
							</table>
						</div>
					</div>
				</article>
				<div class="buttons-complete">
					<div class="buttons">
						<button type="submit" class="button button__save">저장</button>
					</div>
				</div>
			</div>
		</section>
	</div>
</template>

<script>
import { selectClientList } from '@/api/client';
export default {
	created() {
		this.selectClientList();
	},
	data() {
		return {
			pagingVO: {
				pageNo: 0,
				searchKind: '1',
			},
			clientList: [],
		};
	},
	methods: {
		async selectClientList() {
			let res = await selectClientList(this.pagingVO);
			console.log(res);
			if (res.result == 0) {
				this.clientList = res.data;
			}
		},
	},
};
</script>

<style></style>
